@use '../base/mixins' as m;
@use '../base/variables' as v;

.descargar {
 padding: 4rem 0;

 @include m.tablet {
  @include m.grid(2, 3rem);
  align-items: center;
 }
}

.descargar__info {
  grid-column: 2/3;
}

.descargar__title {
 margin-bottom: 1rem;
}

.descargar__disponible {
  text-align: center;
  margin-bottom: 3rem;
}

.descargar__tiendas {
 @include m.grid(2, 3rem)
}

.tienda__enlace {
  margin-bottom: 2rem;
  transition-property: transform;
  transition-duration: .5s;

  &:hover {
    transform: scale(1.2);
  }
}

.descargar__imagen {
  width: 60%;
  margin: 0 auto;
  grid-row: 1/2;
  transition-duration: .3s;
  transition-property: transform;

  &:hover {
    transform: rotate(3deg) scale(1.1);
  }
}


