@use '../base/mixins' as m;
@use '../base/variables' as v;


.footer {
  background-color: v.$gris;
  position: relative;
  margin-top: 5rem;



  &::before {
    content: '';
    position: absolute;
    width: 110%;
    height:9rem;
    background-color: v.$gris;
    top:-5rem;
    left: 0;
    transform: rotate(-2deg);
   
  }
}

.footer__contenido {
  color: v.$blanco;
  padding: 4rem 0;
  
 @include m.tablet{
  @include m.grid(2, 3rem);
  padding: 8rem 0;
}
}

.footer__nosotros {
 margin-bottom: 3rem;
}

.footer__title {
  padding: 3px 1rem;
  border-radius: 5px;
 color:v.$rojo;
 border-bottom: 1px dotted #fff;
 display: inline-block;
}

.footer__info {

}

.footer__nav {

}

.footer__enlaces {
  display: flex;
  flex-direction: column;
}

.footer__enlace { 
  margin-bottom: 1rem;
  font-weight: normal;
}

.footer__creditos {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bold;
  font-size: 1.8rem;
  color: v.$blanco;
  margin: 0;
  text-align: center;
  padding: .5rem;
  background-color: v.$rojo;
}