
@use 'variables' as v;


html{
  font-size: 62.5%;
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: inherit;
}


body {
  font-size: 1.6rem;
  background-color: v.$blanco;
  
}
a {
  text-decoration: none;
  font-size: 2rem;
}
p{
  font-size: 1.5rem;
  line-height: 1.7;
  text-align: left;
}


h1,h2,h3 {
  margin:0 0 2rem;
}

h1 {
  font-size:4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

img {
  max-width:100%;
  height: auto;
  display: block;
  margin:0 auto;
  
}

span{
  font-weight: bold;
}

a{
  text-decoration: none;
  color: v.$blanco;
  font-size: 2rem;
  font-weight: 600;
}

li {
 list-style: none;
}

a.activo {
 background-color: v.$rojoC;
}

.contenedor {
  width: 90%;
  max-width:120rem;
  margin: 0 auto;
  text-align: center;
}


.text-center {
  text-align: center;
}

.capitalize {
  text-transform: capitalize;
}

.mt-5{
  margin-top: 5rem;
}
.mt-10{
  margin-top: 10rem;
}

.mb-5{
  margin-bottom: 5rem;
}
.mb-10 {
  margin-bottom: 10rem;
}

.btn {
  padding: .5rem 1rem;

  border-radius: 5px;  
} 

