@use '../base/mixins' as m;
@use '../base/variables' as v;

.form {

}
.form__grid {

  @include m.tablet {
  @include m.grid(2, 2rem);
  align-items: center;   
  }
}

.form__contenido {
  color: v.$blanco;
  border-radius: 20px 20px 0 0;
  background-color: v.$rojo;
  padding: 6rem 3rem;
  margin-bottom: 3rem;


  @include m.tablet {
    margin:0;
  }
}

.form__heading {
  margin-bottom: 5rem;
}

.form__label {
  padding: 1rem 0;
  font-size: 2rem;
  display: block;
  text-align: left;
}

.form__input {
  text-align: center;
  border: none;
  padding: 1rem  0;
  width: 100%;
}
.form__input[type="submit"]{
  font-weight: bold;
  font-size: 2rem;
  margin-top: 3rem;
  background-color: v.$gris;
  color: v.$blanco;
  border-radius: 5px;
}

.form__imagen {
  margin: 0 auto;
  width: 25rem;

  @include m.phone {
    width: 40rem;
  }

  @include m.pc{
    width: 55rem;
  }
}