@use '../base/mixins' as m;
@use '../base/variables' as v;

.pasos {
  position: relative;
  margin-top: 20rem;
 
}


.contenido__pasos {
  padding-bottom: 5rem;
  background-color: v.$rojo;
  color:v.$blanco;

}

.pasos__heading {
text-align: center;
}

.pasos__heading--desciption {
  margin-bottom: 5rem;
  padding: 1rem 0;
  font-size: 2rem;
  display: block;
}

.pasos__cards {
  @include m.tablet {
    @include m.grid(3, 1rem);
  }

}

.card {
  transition-property: transform;
  transition-duration: .3s;
}

.card:hover {
  transform: rotate(-3deg) scale(1.2);
}

