@use '../base/mixins' as m;
@use '../base/variables' as v;


.testimoniales {
  padding: 5rem 0;
}

.testimoniales__title {

}

.testimoniales__grid {

  @include m.pc {
    @include m.grid (5, .5rem);
  }

}

.testimonio {
  font-size: 1.8rem;
  color:v.$blanco;
  border-radius:5px;
  padding: 1.5rem;
  background-color: v.$gris;
  margin-bottom: .5rem;

  @include m.tablet {

    &:first-child{
      grid-column: 1/4;  
     }
     &:nth-child(3){
      grid-column: 5/6;
      grid-row: 1/3;
     }

     &:nth-child(4){
      grid-column: 1/3;
     }
     &:nth-child(5){
      grid-column: 3/5;
     }

   
    

    }
}

.testimonio__header {
  display: flex;
  gap: 2rem;
  align-items: center;
}

.testimonio__imagen {
  border-radius: 50%;
  flex-basis: 10rem;
  overflow: hidden;

}

.testimonio__info {
  flex-basis: 20rem;
}

.testimonio__nombre {
  font-weight: bold;
  margin-bottom: 0;
}

.testimonio__calificacion {
 
}

.testimonio__texto {
  text-align: justify;
  position: relative;
}

.testimonio__texto::before {
  position: absolute;
  font-size: 7rem;
  color: v.$rojo;
  background-image: url(build/img/comillas.svg);
  content: '"';
  top:-1.7rem;
  left: -2.9rem;
  display: block;
  opacity:0.5 ;
  
  
}