
@use '../base/variables' as v;
@use '../base/mixins' as m;

.header {
  padding: 4.5rem 0;
  @include m.pc {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

}

.header__contenido {

  @include m.pc {
    display: flex;
    flex-direction: column;
  }
}

.header__logo {
  margin: 0 auto;
  margin-bottom: 5rem;
  width: 25rem;

  @include m.pc {
    margin:0;
  }

}

.header__nav {
  display: flex;
  flex-direction: column;
  gap: 1.7rem;
  align-items: center;
  
  @include m.pc{
    flex-direction: row;
    gap:2rem;
  }

}

.nav__enlace {
  color: #000;
}

.nav__enlace--registrar{
  width: 100%;
  padding: .7rem 1rem ;
  border-radius: 10px;
  color:v.$blanco;
  background-color: v.$rojo;

  @include m.tablet {
    padding:.9rem 2rem;
    width: auto;
  }
}