@use '../base/mixins' as m;
@use '../base/variables' as v;

.restaurantes  {
  background-color:rgb(242, 242, 242)
}

.restaurantes__contenido {
  padding: 5rem 0;
}

.restaurantes__titulo {
  color: v.$gris;
}
.restaurantes__grid {
  @include m.tablet {
    @include m.grid(2, 3rem);
  }
}

.restaurante{
  padding: 4rem 6rem;
  background-color: v.$blanco;
  border-radius: 100px 5px 130px 5px;
  margin-bottom: 2rem;
  box-shadow: 0px 0px 2.6rem -0.8rem #99a4a9;


  @include m.tablet {
    @include m.grid(2,3rem);
    align-items: center;
    padding: 4rem 2rem;
  }

  @include m.pc {
    padding: 4rem;
  }
}


.restaurante__imagen {
 max-width: 30rem;
 margin: 0 auto 2rem auto;

}

.resturante__info {
 display: flex;
 flex-direction: column;
 align-items: center;
 
}

.restaurante__estrellas {
  margin-top: 1rem;
  max-width: 20rem;

}

.restaurante__title {
  margin-bottom: 0;
  margin-top: 1rem;
 color: v.$rojo;
}

.restaurante__texto {
  margin:1rem 0;
}